<template>
    <div class="timer-bar-container">
        <div class="timer-bar-total"></div>
        <div class="timer-bar-current" :data-time-left="state.timeLeft"></div>
    </div>
</template>

<script setup>
import { state } from '../module/state.js'

window.state = state;
</script>

<script>
export default {
    methods: {
        computeStyle() {
            return `width: ${(state.timeLeft / state.timeTotal) * 100}%;`;
        }
    }
}
</script>

<style scoped>
    .timer-bar-current {
        width: v-bind('state.timeWidth');
        height: 40px;
    }

    .timer-bar-container, .timer-bar-total {
        width: 100%;
        height: 40px;
    }

    .timer-bar-total {
        background-color: v-bind('state.barColor');
    }

    .timer-bar-current {
        background-color: #ff0;
        position: relative;
        top: -40px;
    }

</style>