import { reactive } from 'vue'

export const state = reactive({
  questionCount: 0,
  answerCount: 0,
  completePercent: 0,
  finishPageZIndex: -1,
  finishPageOpacity: 0,
  timeLeft: 4,
  timeTotal: 8,
  timeWidth: '50%',
  operandA: 12,
  operandB: 9,
  product: 108,
  productVisible: 'auto',
  barColor: '#ddd',
  beginTimer(limit) {
      this.timeTotal = limit;
      this.timeLeft = limit;
      this.timeWidth = '100%';
  },
  decreaseTimer(value) {
      this.timeLeft -= value;
      this.timeWidth = `${Math.round(this.timeLeft / this.timeTotal * 100, 2)}%`;
  }
});