<template>
    <div class="parent-container">
        <div class="main-panel">
            <TimerBar></TimerBar>
            <MathQuestion></MathQuestion>
            <MathAnswer></MathAnswer>
            <div class="button-panel">
                <div class="action-button next-question" @click="nextQuestion">Got it!</div>
                <div class="action-button stop" @click="stop">Stop</div>
            </div>
        </div>
        <FinishPage></FinishPage>
    </div>
</template>

<script setup>
import { state } from './module/state'
</script>

<script>
import MathQuestion from './components/MathQuestion.vue'
import TimerBar from './components/TimerBar.vue'
import MathAnswer from './components/MathAnswer.vue'
import FinishPage from './components/FinishPage.vue'

let operandALowerLimit = 0;
let operandAUpperLimit = 12;
let operandBLowerLimit = 0;
let operandBUpperLimit = 12;
let questionTimerLimit = 10000;
let questionDelay = 2000;

let questionCount, answerCount = 0;

let questionList = [], questionIndex = 0;

for (let m = operandALowerLimit; m <= operandAUpperLimit; m++) {
    for (let n = operandBLowerLimit; n <= operandBUpperLimit; n++) {
        questionList.push([m , n]);
    }
}

questionCount = questionList.length;

let timerHandle, nextHandle, timerStart;

export default {
    name: 'App',
    components: {
        TimerBar,
        MathQuestion,
        MathAnswer,
        FinishPage
    },
    methods: {
        nextQuestion() {
            console.log('Next');
            answerCount++;
            timerHandle = false;
            clearTimeout(nextHandle);
            this.setUpNextQuestion();
        },
        stop() {
            console.log('Stop');
            timerHandle = false;
            clearTimeout(nextHandle);
        },
        complete() {
            console.log('Complete');
            state.barColor = '#0f0';
            state.answerCount = answerCount;
            state.questionCount = questionCount;
            state.completePercent = Math.round(answerCount / questionCount * 100) >>> 0;
            state.finishPageZIndex = 1;
            state.finishPageOpacity = 1;
        },
        setUpNextQuestion() {
            if (questionList.length < 1) {
                this.complete();
                return;
            }
            let indexAdvance = (Math.random() * questionList.length) >>> 0;
            questionIndex += indexAdvance;
            if (questionIndex >= questionList.length) {
                questionIndex -= questionList.length;
            }

            let question = questionList.splice(questionIndex, 1)[0];
            state.productVisible = '0';
            state.operandA = question[0];
            state.operandB = question[1];
            state.product = (question[0] * question[1]) >>> 0;
            state.barColor = '#ddd';
            state.beginTimer(questionTimerLimit);
            window.requestAnimationFrame(this.timerAdvance);
            timerStart = Date.now();
            timerHandle = true;
        },
        timerAdvance() {
            if (!timerHandle) return;
            let now = Date.now();
            state.decreaseTimer(now - timerStart);
            timerStart = now;
            if (state.timeLeft <= 0) {
                this.endQuestion();
            }
            else {
                window.requestAnimationFrame(this.timerAdvance);
            }
        },
        endQuestion() {
            timerHandle = false;
            nextHandle = setTimeout(this.setUpNextQuestion, questionDelay);
            window.nextHandle = nextHandle;
            state.barColor = '#f00';
            state.productVisible = 'auto';
        }
    },
    mounted() {
        this.setUpNextQuestion();
    }
}
</script>

